.filter-div {
    border-top: 1px solid var(--grey);
    padding: 1rem 3rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow-x: auto;
    position: sticky;
    top: 4.5rem;
    background-color: var(--white);
    z-index: 1000;
  }
  
  .links-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    opacity: 0.5;
    border-bottom: 1px solid var(--white);
    min-width: 85px;
  }
  .links-box:hover,.selected-box
    {
        opacity: 1;
   border-bottom: 2px solid var(--black);
  transition: all 0.3s;
  cursor: pointer;
}
.links-img {
    width: 1.5rem;
    height: 1.5rem;
    object-fit: contain;
  }
  
  .links-label {
    font-size: 0.7rem;
    font-weight: 600;
    color: var(--font-grey);
  }
  .selected-label{
    color: var(--black);

  }

  @media only screen and (max-width: 700px) {
    .filter-div {
      padding: 0.5rem;
      padding-top: 1.5rem;
    }
  }